import { useParams } from "react-router-dom";
import { Container, Row, Button, Col, Nav } from 'react-bootstrap'
import { useState } from "react";
import TabContent from "./Tab";

import { addItem } from "./store";
import {  useDispatch } from "react-redux";
import $ from 'jquery';

function Page3(props) {
    let { id } = useParams(0);
    let [tab, setTab] = useState(0);
    let selproduct=props.furnitureItem.find((x)=> x.id==id);
    let dispatch=useDispatch();
    const [text, setText]=useState('');
    
    let handleChange=(e)=>{
        setText(e.target.value);
      
    };
    let optChange=(e)=>{
        setOption1(e.target.value);
    };
    let opt2Change=(e)=>{
        setOption2(e.target.value);
    }
    const [option1, setOption1]=useState('');
    const [option2, setOption2]=useState('');




    return (
        <>
            <Container className="info-0">

                <Row className="item-box">

                    <Col md={6}>

                        <img src={props.furnitureItem[id].imgUrl} width="90%" alt="" />

                    </Col>

                    <Col md={6} className='item-text'>

                        <h4 className="item-title">{props.furnitureItem[id].title}</h4>

                        <p>{props.furnitureItem[id].content}</p>
                        <div className="flex_ item-price">
                            <span>판매가</span><p>{props.furnitureItem[id].price}</p>
                        </div>
                        <div className="flex_ item-price">
                            <span>공간실측</span>
                            <input id="name" onChange={handleChange} value={text} required
                                minlength="4" maxlength="8" size="10"

                                placeholder='사이즈 가로X세로' />
                        </div>
                        <div className="res-text">[필수] 
                            <div className="result1 res">{text}</div>
                        </div>
                        <div className="flex_ item-price">
                            <span>질감</span>
                           <select className="kind" onChange={optChange}>
                           <option>옵션을 선택해주세요</option>
                           <option value='무광'>무광</option>
                            <option value='유광'>유광</option>
                           </select>
                        </div>
                        
                        <div className="res-text">[필수] 
                            <div className="result2 res">{option1}</div>
                        </div>
                        <div className="flex_ item-price">
                            <span>디자인</span>
                            <select className="kind" onChange={opt2Change}>
                                <option>옵션을 선택해주세요</option>
                                <option>기본핀형(평식)1마~</option>
                                <option>나비주름핀형(호텔식)1마~(+2,000원)</option>
                            </select>
                        </div>
                        <div className="res-text">[필수] 
                            <div className="result3 res">{option2}</div>
                        </div>

                        <div className="price flex_">
                           <p className="al-op">{text} {option1} {option2}</p> <span>{props.furnitureItem[id].price}</span>
                        </div>
                        <div className="btn-box2 flex_">
                            <p className="hart2 flex_"><img src="https://mixxo.com/morenvyimg/top_wish.svg"/></p>
                            <Button variant="light" onClick={()=>{
                              dispatch(addItem({
                                img:selproduct.imgUrl,
                                name:selproduct.title,
                                option: text+'/'+option1+'/'+option2,
                                count:1
                               
                            }))
                            }}>장바구니</Button>{' '}
                            <Button variant="danger">주문하기</Button>
                        </div>

                    </Col>

                </Row>



            </Container>

            <hr />

       <TabContent tab={tab} detail={props.detail}/>
 

        </>

    )
}

export default Page3;