import { useState } from 'react';

import { useParams } from 'react-router-dom';

import { Button, Table } from 'react-bootstrap';

import { useDispatch, useSelector } from "react-redux";

import { addCount, minusCount, sortName } from "./store.js";
import $ from 'jquery';


function Cart(props) {
    let state = useSelector((state) => state)
    let dispatch = useDispatch();

    // all check
    $('.ch-box>input').on('click',function () {
        if ($('.ch-box>input').is(':checked')) {
            $('.ch-box>input').prop('checked', true);
        } else {
            $('.ch-box>input').prop('checked', false);
        }
    });

    $('#al-box').on('click',function () {
        if ($('.ch-box>input').is(':checked')) {
            $('.ch-box>input').prop('checked', true);
        } else {
            $('.ch-box>input').prop('checked', false);
        }
    });

    // $('#af').on('click',function () {
    //     if ($('.ch_b>input').is(':checked')) {
    //         $('.se-ul>li>input').prop('checked', true);
    //     } else {
    //         $('.se-ul>li>input').prop('checked', false);
    //     }
    // });

    return (
        <div className='cart-app'>

            <h2>장바구니</h2>
            <Table>
                <caption>일반상품(0)</caption>
                <thead>

                    <tr>

                        <th><input type={'checkbox'} className='alck' id='al-box'/></th>

                        <th>상품정보</th>
                        <th>수량</th>

                        <th>배송비</th>
                        <th>합계</th>
                        <th>선택</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        state.cart.map((v, i) =>
                            <tr key={i}>
                                <td className='ch-box'><input type={'checkbox'} /></td>

                                <td className='to'>
                                    <img className='cart-img' src={state.cart[i].img} />

                                    <div className='option'>
                                        <p>{state.cart[i].name}</p>
                                        <p>옵션{state.cart[i].option}</p>
                                        <button>옵션변경</button>
                                    </div>
                                </td>
                                <td className='p_m'>
                                    <button onClick={() =>
                                        dispatch(minusCount(state.cart[i].id))}>-</button>
                                    <p>{state.cart[i].count}</p>
                                    <button onClick={() =>
                                        dispatch(addCount(state.cart[i].id))}>+</button>

                                </td>

                                <td className='shipping'>무료</td>
                                <td></td>
                                <td className='t_btn'>
                                    <button>주문하기</button>
                                    <button>삭제</button>
                                </td>

                            </tr>
                        )
                    }
                </tbody>
                <tfoot>

                    <tr>
                        <td colSpan={6} className='tf-all'>
                            상품구매금액 <span className='price'>0</span> + 배송비 0 (무료) = 합계: <span className='all_sum'></span>
                        </td>
                    </tr>
                </tfoot>

            </Table>
            <div className='delet'>
                <button>선택상품 삭제</button>
                <button>장바구니 비우기</button>
            </div>



            <div className='all_price'>
                <table>
                    <thead>
                        <tr>
                            <th>총 상품 금액</th>
                            <th></th>
                            <th>총 배송비</th>
                            <th></th>
                            <th>총 할인금액</th>
                            <th></th>
                            <th>결제 예정 금액</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td><sapn className='price'>0</sapn></td>
                            <td>+</td>
                            <td>0 (무료배송)</td>
                            <td>-</td>
                            <td>0</td>
                            <td className='black'>=</td>
                            <td><sapn className="sum_price"></sapn></td>
                        </tr>

                    </tbody>
                </table>

                <button>쇼핑계속하기</button>
                <button>선택상품주문</button>
                <button className='al-box'>전체상품주문</button>


            </div>


        </div>
    )

}


export default Cart;