import { Nav, Container, Row, Col, Button } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';


function Menu_list(props) {
    let Navigate = useNavigate();
    return (
        <>

            <Col md={3} className='furni'>
            <Nav.Link onClick={()=>{
                  Navigate('/Page3/'+props.res[props.i])
                  }}>
                <img src={props.furnitureItem.imgUrl} alt='#' width='90%' />
                <div className='furni-text'>
                    <h5>{props.furnitureItem.title}</h5>
                    <p>{props.furnitureItem.price}</p>

                </div>
                </Nav.Link>
            </Col>

        </>


    )


}

export default Menu_list;