import { Col } from 'react-bootstrap'


function Recommendation(props) {
    return (
        <>
            <Col className='reco_img_box' lg={3}>

                <div className='reco-img-text'>
                    <img className='reco_img' src={props.recommendation_item.imgUrl} width='70%' alt='#' />
                    <p className='reco-text'>{props.recommendation_item.title}</p>
                </div>

                <h5>{props.recommendation_item.title}</h5>
                <h6>{props.recommendation_item.contents}</h6>

                <a href='#'>VIEW MORE +</a>
            </Col>


        </>
    )
}



export default Recommendation;