import { Nav, Container, Row, Col, Button, Dropdown } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';
import Menu_list from './Menu_list';
import Page1 from './page1';
import Page3 from './page3';

function Furniture(props) {
    let [res, setRes] = useState([0, 1, 2, 3, 4, 5, 6, 7, 8]);
    let Navigate = useNavigate();
    
    function Tablelist() {

    }


    return (
        <div className='furniture'>
            <h3>furniture</h3>
            <div className='list-ul flex_'>
                <ul className='flex_'>
                    <li>테이블</li>
                    <li>/</li>
                    <li>콘솔</li>
                    <li>/</li>
                    <li>의자</li>
                    <li>/</li>
                    <li>선반</li>
                    <li>/</li>
                    <li>거울</li>
                    <li>/</li>
                    <li>기타</li>
                </ul>
            </div>
            <div className='rank flex_'>
                <ul className='flex_ rank-ul'>
                    <li onClick={() => {
                        let copy = [...props.furnitureItem].sort((a, b) => a.price > b.price ? 1 : -1);
                        props.setFurnitureItem(copy);
                        let res1 = [];
                        for (let i in copy) {
                            res1.push(copy[i].id)
                        }
                        props.setRes(res1);
                    }} >낮은가격</li>{''}

                    <li>/</li>
                    <li onClick={() => {
                        let copy = [...props.furnitureItem].sort((a, b) => a.price < b.price ? 1 : -1);
                        props.setFurnitureItem(copy);
                        let res1 = [];
                        for (let i in copy) {
                            res1.push(copy[i].id)
                        }
                        setRes(res1);
                    }}
                    >높은가격</li>
                    <li>/</li>
                    <li onClick={() => {
                        let copy = [...props.furnitureItem].sort((a, b) => a.title > b.title ? 1 : -1);
                        props.setFurnitureItem(copy);
                        let res1 = [];
                        for (let i in copy) {
                            res1.push(copy[i].id)
                        }
                        setRes(res1);
                    }}>이름순</li>
                </ul >
            </div>

            <Container className='fur-box'>
                <Row>
                    {
                        props.furnitureItem.map((a, i) => {
                            return (
                                <Menu_list furnitureItem={props.furnitureItem[i]} i={i} res={props.res}/>
                            )
                        })
                    }
                </Row>
            </Container>


        </div>

    );

}


export default Furniture;