
import Main from "./Main";
import Product from "./Product";
import { Container, Row, Button, Col } from 'react-bootstrap'
import Title2 from "./Title2";
import { Swiper, SwiperSlide } from 'swiper/react';
import Carousel from 'react-bootstrap/Carousel';
import Recommendation from "./Recommendation";
import { Navigation, Pagination, Scrollbar, A11y, Autoplay, EffectCoverflow } from 'swiper';
import Reviwe from "./Reviwe";
import Page1 from "./page1";
import Cart from "./cart";

function Home(props) {
    return (
        <>
            <div className="slider">
                <img src="https://rlaensk.github.io/missmone/img/slider.PNG" alt="#" />
                <img className="imgtext" src="http://www.missmone.com/design/missmone/onedesign/images/bg_main_text.png" alt="#" />

            </div>
            <div className='Main-box'>
                <h3>이번달 신제품</h3>

                <Swiper className='swiper-box mySwiper'
                    slidesPerView={4}
                    spaceBetween={30}
                    loop={true}
                    loopadditionalslides={1}
                    breakpoints={{
                        320:{
                            
                            slidesPerView:1,
                            spaceBetween:0
                        },
                        701:{
                            slidesPerView:2,
                        },
                        1001:{
                            slidesPerView:4,
                            spaceBetween:30
                        }
                    }}
                    pagination={{
                        clickable: true,
                    }}
                    navigation={true}
                    autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                    }}
                    modules={[Pagination, Navigation, Autoplay]}
                    paginationHide={true}
                >

                    {
                        props.item1.map((a, i) => {
                            return (
                                <SwiperSlide className='swiper-img-box'>
                                    <Main item1={props.item1[i]} i={i}  res={props.res} />
                                </SwiperSlide>

                            )
                        })
                    }
                </Swiper>
            </div>


            <div className="benner flex_">
                <img src="https://rlaensk.github.io/missmone/img/benner2.png" alt="" />
                <img src="https://rlaensk.github.io/missmone/img/benner1.png" alt="" />
            </div>

            <Title2 />

            <Container className="Pro-item-box">
                <Row>
                    {
                        props.item.map((a, i) => {
                            return (
                                <Product item={props.item[i]} i={i} res={props.res} />
                            )
                        })
                    }
                </Row>
            </Container>




            <Container className="bg_t">
                <Row className="reco_box">
                    {
                        props.recommendation_item.map((a, i) => {
                            return (
                                <Recommendation recommendation_item={props.recommendation_item[i]} i={i + 1} />
                            )
                        })
                    }
                </Row>
            </Container>






            <div className='re-box'>
                <h3>리뷰 인기템</h3>

                <Swiper className='rev-item-box mySwiper'
                    slidesPerView={5}
                    spaceBetween={10}
                    centeredSlides={true}
                    loop={true}
                    loopadditionalslides={1}
                    breakpoints={{
                        320:{
                            
                            slidesPerView:1,
                            spaceBetween:0
                        },
                        701:{
                            slidesPerView:3,
                            spaceBetween:20
                        },
                        1001:{
                            slidesPerView:5,
                            spaceBetween:10
                        }
                    }}
                    // pagination={{
                    //     clickable: true,
                    // }}
                    navigation={true}
                    autoplay={{
                        delay: 3500,
                        disableOnInteraction: false,
                    }}
                    modules={[Pagination, Navigation, Autoplay]}

                >

                    {
                        props.re_item.map((a, i) => {
                            return (
                                <SwiperSlide className='re-img-box'>
                                    <Reviwe re_item={props.re_item[i]} i={i + 1} />
                                </SwiperSlide>

                            )
                        })
                    }
                </Swiper>
            </div>
            <div className="last-benner">
                <div className="benner1">
                    <img className="flower-benner" src="img/flower.png" alt="" />
                    <button> VIEW MORE</button>
                </div>
                <div className="benner2">
                    <img className="flower-benner" src="img/vin.png" alt="" />
                    <button> VIEW MORE</button>
                </div>
            </div>

        
        </>

    )
}


export default Home;