function TabContent(props) {
    // let [tab, setTab] = useState(0);

    return (
        <Container className="info-1">
            <Row>
     

                <Tabs
                    defaultActiveKey="link0"

                    className="mb-3"
                >
                    <Tab eventKey="link0" title="상세설명">
                        <Detail detail={props.detail} />
                    </Tab>
                    <Tab eventKey="link1" title="상품후기">
                        <Review />
                    </Tab>
                    <Tab eventKey="link2" title="상품문의" >
                        <Qestion/>
                    </Tab>
                </Tabs>
            </Row>

        </Container>
    )
}
function Detail(props) {

    let { id } = useParams();

    return (
        <>
            <Container>
                <Row>
                    <Col className='detail_box'>

                        <img className='det-img' src={props.detail[id].imgUrl1} width='55%' alt='' />
                        <img className='det-img' src={props.detail[id].imgUrl2} width='55%' alt='' />
                        <img className='det-img' src={props.detail[id].imgUrl3} width='55%' alt='' />
                    </Col>
                </Row>
            </Container>
        </>
    )
}

